import React from 'react'
import {Navbar,Nav} from 'react-bootstrap'
import { NavLink } from 'react-router-dom'
function Navx() {
    return (
     <div className="container-fluid nav-bg">
     <div className="row">
     <div className="col-sm-10 mx-auto">
         <Navbar bg="transparent" expand="lg">
  <Navbar.Brand >
  <NavLink className="navbar-brand font-weight-bold" to='/'> Akash Kumar</NavLink>
 </Navbar.Brand>
  <Navbar.Toggle aria-controls="basic-navbar-nav" />
  <Navbar.Collapse id="basic-navbar-nav">
    <Nav className="ml-auto">
    <NavLink activeClassName="menu_active" exact className="nav-link " aria-current="page" to='/'>Home</NavLink>
    <NavLink activeClassName="menu_active" className="nav-link " to='/service' >Service</NavLink>
    <NavLink activeClassName="menu_active" className="nav-link" to='/about'>About</NavLink>
    <NavLink activeClassName="menu_active" className="nav-link" to='/contact'>Contact</NavLink>
    </Nav>
  </Navbar.Collapse>
</Navbar>
</div>    
     </div>

     </div>   
    
    )
}

export default Navx
