import React from 'react'
import { NavLink } from 'react-router-dom';

function Common(props) {
    return (
        <div>
     <section id="header" className="home">
      <div className="container-fluid">
       <div className="row">
        <div className="col-10 mx-auto">
        <div className="row"> 
            <div className="col-md-6 pt-5  order-2 pt-lg-0 order-lg-1 word d-flex flex-column justify-content-center">
            <h1>{props.name}<strong className="brand-name">Akash</strong></h1>
            <h2 className="my-3"> We are the team of talented developer making websites</h2>
            <div className="mt-3"> 
            <NavLink to={props.vist} className="btn btn-outline-dark rounded-pill">{props.btname}</NavLink>
            </div></div>
            <div className="col-lg-6 order-1 order-lg-2 justify-content-center" >
            <div className={props.classsvg}>
            <img src={props.imgsrc} className="img-fluid animated" alt="home" />            
            </div>
            </div>
            
        </div>   
       </div>   
      </div>  
      </div> 
     </section>
        </div>
    )
}

export default Common;