import React from 'react'
import {Card,Button} from 'react-bootstrap'

function Card1(props) {
    return (    
  <div className="col-md-6 col-lg-4 col-10 mx-auto py-2 d-flex justify-content-center my-sm-3" >
         <Card border="info" style={{ width: '24rem'}} >
  <Card.Img variant="top" src={props.imgsrc} style={{height:'210px'}} />
  <Card.Body>
    <Card.Title>{props.title}</Card.Title>
    <Card.Text>
      Some quick example text to build on the card title and make up the bulk of
      the card's content.
    </Card.Text>
    <Button variant="outline-primary">Get Start</Button>
  </Card.Body>
</Card>   
         </div>   
       
    )
}

export default Card1
