import React from 'react'
import Card1 from './Card1'
import Sdata from './Sdata'

function Service() {
    return (
        <div>
             <div><h1 className="text-center">Our Service</h1>   
                                                                </div>
        <div className="container-fluid">
       <div className="row">
        <div className="col-sm-10 mx-auto">
        <div className="row gy-4">
        {Sdata.map((val, index)=>{
         return <Card1 key={index} imgsrc ={val.imgsrc}
         title ={val.title} 
        
         />  
        })}
        </div>

        </div>
        </div>
        </div>

        
        </div>
    )
}

export default Service
