import React ,{useState} from 'react'
import {Form} from 'react-bootstrap'
function Contact() {
    const [data, setData] = useState({
     fullname:"",
     phone:"",
     email:"",
     msg:"",   
    });
    const formSubmit = (e) =>{
        e.preventDefault();
        alert(
         `My name is ${data.fullname} . My mobile number is ${data.phone} and email is 
         ${data.email} , Here is what I want to say ${data.msg} `
        );

    }
    const InputEvent = (event) =>{
        const {name,value} = event.target;
        setData((preVal)=>{
        return{
         ...preVal,[name]:value,   
        };});
        
    }

    return (
        <div>
        <div className="my-5">
        <h1 className="text-center my-4"> Contact Us</h1>
       
        <div className="container contact-div">
         <div className="row">
          <div className="col-md-6 col-10 mx-auto">
          <Form >
  <Form.Group controlId="exampleForm.ControlInput1">
    <Form.Label>Full Name</Form.Label>
    <Form.Control type="text" name="fullname" value={data.fullname} onChange={InputEvent} placeholder="Write your name" />
     <br/>
    <Form.Label>Email address</Form.Label>
    <Form.Control type="text" name="email" value={data.email} onChange={InputEvent} placeholder="name@example.com" />
   <br/>
    <Form.Label>Phone Number</Form.Label>
     <Form.Control type="text" name="phone" value={data.phone} onChange={InputEvent} placeholder="999XXXXXXX" />
  <br/>
  </Form.Group>
  <Form.Group controlId="exampleForm.ControlTextarea1">
    <Form.Label>Message</Form.Label>
    <Form.Control as="textarea" name="msg" rows={3} value={data.msg} onChange={InputEvent}/>
  </Form.Group>
  <div className="btn btn-outline-dark" type="submit" onClick={formSubmit}>
    Submit
  </div>
</Form>
          </div>   
         </div>   
        </div>
        </div>
        </div>
    )
}

export default Contact
